import './App.css';
import bgImage from '../src/bg.png';
import {useState} from 'react';
import {Fade,Bounce} from '../node_modules/react-reveal';
const totaltext=[
  "I was so fortunate to have you as my mentor during my internship. Thank you for all you did to make my 3months at Cimpress a valuable learning experience. The hours you spent with me helped me in developing my skillset and made me a better person personally and professionally.",
"By giving motivational speeches, you pushed me everyday to work even harder. Learning the best practices in developing a software, correcting me when I did mistakes, Thursday sync ups will all be missed.",
"Not only did you provide career guidance during the internship, but also cultivated my hobbies.",
"Once again, thanks for sharing your time, knowledge, and advice during my internship. I wish you all the best and have a Cimpressive journey ahead!! - Shishira S K",

]
function App() {
  const [text,setText]=useState(totaltext[0]);
  const [count,setCount]=useState(1);
  const handleScroll = () =>{
      if(count===totaltext.length){
        setText(totaltext[0]);
        setCount(0);
        return;
      }
      setText(totaltext[count]);
      setCount(count+1);
  }
  return (
    <div className="App">
      <img src={bgImage} alt="bg" className="my"></img>
     <Bounce left><div className="centertext"><b>Thank you Cullin!!</b></div></Bounce>
     <Fade up spy={text} appear={true}><p className="text" onClick={handleScroll}> {text}</p></Fade>
    </div>
  );
}

export default App;
